import React from 'react'
import "../CSS/navbar.css"
import {Box, Flex, HStack, Text} from "@chakra-ui/react"
function Navbar() {
  return (
    <Box h="auto" flexWrap="wrap" >
        <Flex justifyContent="space-between" bgColor="#e9ad35" p={5}>
            <Text ml={10} as="b" fontSize={35} fontFamily="BestermindRegular" color="#eaeaea" cursor="pointer">Pg. </Text>
            <HStack mr={10} spacing={10}>
                <Text as="b" fontSize={20} color="#eaeaea" cursor="pointer">About</Text>
                <Text as="b" fontSize={20} color="#eaeaea" cursor="pointer">Skills</Text>
                <Text as="b" fontSize={20} color="#eaeaea" cursor="pointer">Projects</Text>
                <Text  as="b" fontSize={20} color="#eaeaea" cursor="pointer">Contact</Text>
            </HStack>
        </Flex>
    </Box>
  )
}

export default Navbar